.app__card-py{
    background: #B2B2B2;
    position: absolute;
    left: 2.5rem;
    top: 41rem;
    border-radius: 2.2rem;
    box-shadow: 0rem 0rem 2rem .3rem;
    overflow: hidden;
    width: 20rem;
    height: 21.9rem;
    padding: 1rem;


    img {
        position: absolute;
        left: 4.5rem; 
        width: 13rem;
        height: 12rem;
        border-radius: 10rem;
    }
}

.app__body-body-py{
    position: absolute;
    top: 10rem;
    
    .title{
        color: red;
        position: relative;
        top: 3.3rem;
        left: 4.2rem;
        font-weight: 600;
        text-transform: uppercase;
    }

    .text {
        color: red;
        position: relative;
        top: 2.4rem;
        padding: 1rem;
        text-align: center;
        
    }

    .btn1, .btn2, .btn3, .btn4, .btn5 {
        position: relative;
        top: 2rem;
        left: 5rem;
    }



    
}





